import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue';
import graceMaskView from '../../graceUI/components/graceMaskView.vue';
var loginRes, _self;
/*  */
var _self, x, y;
export default {
	components: {
		uniSwipeAction
	},

	data() {
		return {
			// /*  */
			// //2个按钮的宽度
			// btn1Width : 70,
			// btn2Width : 70,
			// //2个按钮的总宽 = btn1Width + btn23Width【uni-app 模板内不建议复杂运算请手动配置】
			// deleteBtnWidth : 140,
			// //正在滑动的索引
			// scrollIndex : -1,
			// //正在删除的索引
			// deleteIndex : -1,
			options: [
				{
					text: '删除',
					style: {
						backgroundColor: '#dd524d'
					}
				}
			],

			storageData: {
				userId: 0,
				login: false
			},
			/* storageData: {}, */
			avatar: '',
			nickname: '',
			//分类信息
			categories: [
				{
					cateid: 0,
					name: '文章'
				},
				{
					cateid: 1,
					name: '关注'
				},
				{
					cateid: 2,
					name: '粉丝'
				},
				{
					cateid: 3,
					name: '收藏'
				},
				{
					cateid: 4,
					name: '签到'
				}
			],
			// 当前选择的分类
			cateCurrentIndex: 0,
			articles: [],
			follows: [],
			like: [],

			/* 遮罩层 */
			staticUrl: this.staticUrl,
			show: false,
			show2: false,
			followed: true
		};
	},
	/* onLoad: function() {}*/
	onLoad: function() {
		_self = this;
	},
	onPullDownRefresh:function(){
		this.deletArticles();
	},
	onShow: function() {
		var _this = this;
		const loginKey = uni.getStorageSync('login_key');
		if (loginKey) {
			this.storageData = {
				login: loginKey.login,
				nickname: loginKey.nickname,
				avatar: loginKey.avatar,
				userId: loginKey.userId
			};
			/* uni.request({
				
				url: this.apiServer + '/article/user_count',
				method: 'GET',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					userId: this.storageData.userId
				},
				success: res => {
					_this.articleCount = res.data.data;
				}
			}); */
			uni.request({
				url: this.apiServer + '/article/user',
				method: 'GET',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					userId: this.storageData.userId
				},
				success: res => {
					_this.articles = res.data.data;
				}
			});
			uni.request({
				url: this.apiServer + '/follow/list',
				method: 'GET',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					fromUId: this.storageData.userId
				},
				success: res => {
					_this.follows = res.data.data;
				}
			});
		} else {
			this.storageData = {
				login: false
			};
		}
		uni.request({
			url: 'http://localhost:8080/api/user/' + uni.getStorageSync('login_key').userId,
			method: 'GET',
			header: { 'content-type': 'application/json' },
			success: res => {
				if (res.data.code === 0) {
					console.log(res.data.data.avatar + '————————————');
					_this.avatar = res.data.data.avatar;
					_this.nickname = res.data.data.nickname;
				}
			}
		});
	},
	methods: {
		deletArticles: function(id) {
			var _this = this;
			uni.request({
				method: 'DELETE',
				url: 'http://localhost:8080/api/article/user?id=' + id,
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				success: res => {
				
				},
				complete: function() {
					uni.stopPullDownRefresh();
				}
			});
		},

		handclick: function() {
			this.followed = !this.followed;
		},
		handleTime: function(date) {
			var d = new Date(date);
			var year = d.getFullYear();
			var month = d.getMonth() + 1;
			var day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate();
			var hour = d.getHours() < 10 ? '0' + d.getHours() : '' + d.getHours();
			var minutes = d.getMinutes() < 10 ? '0' + d.getMinutes() : '' + d.getMinutes();
			var seconds = d.getSeconds() < 10 ? '0' + d.getSeconds() : '' + d.getSeconds();
			return year + '-' + month + '-' + day + ' ' + hour + ':' + minutes + ':' + seconds;
		},
		tabChange: function(e) {
			// 选中的索引
			var index = e.currentTarget.dataset.index;
			// 具体的分类id
			var cateid = e.currentTarget.dataset.cateid;
			this.cateCurrentIndex = index;
			// 动态替换内容
			this.content = this.categories[index].name;
		},
		gotoDetail: function(aId) {
			uni.navigateTo({
				url: '../article_detail/article_detail?aId=' + aId + '&userId=' + this.storageData.userId
			});
		},

		/* 遮罩层 */
		// 第1个演示 开启与关闭
		showBanner: function() {
			this.show = true;
		},
		closeBanner: function() {
			this.show = false;
		},
		// 第2个演示 开启与关闭
		showBanner2: function() {
			this.show2 = true;
		},
		closeBanner2: function() {
			this.show2 = false;
		},
		tap2: function() {
			uni.showToast({
				title: '您点击了红包图片',
				icon: 'none'
			});
		}

		// /*  */
		// removeMsg : function(e){
		// 	var index = e.target.dataset.id;
